"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";

interface Post {
  id: number;
  author: string;
  content: string;
  timestamp: string;
  likes: number;
  comments: number;
  imageUrl?: string;
}

const dummyPosts: Post[] = [
  {
    id: 1,
    author: "Alice Johnson",
    content: "Just finished an amazing workout session! Feeling energized and ready to take on the day. üí™",
    timestamp: "2 hours ago",
    likes: 12,
    comments: 3,
  },
  {
    id: 2,
    author: "Bob Smith",
    content: "Beautiful sunset from my office window today. Sometimes we need to pause and appreciate the simple things in life.",
    timestamp: "4 hours ago",
    likes: 24,
    comments: 7,
    imageUrl: "https://images.pexels.com/photos/1181677/pexels-photo-1181677.jpeg?auto=compress&cs=tinysrgb&w=400"
  },
  {
    id: 3,
    author: "Charlie Brown",
    content: "Excited to announce that our team just launched a new project! Months of hard work finally paying off. Grateful for such an amazing team.",
    timestamp: "6 hours ago",
    likes: 45,
    comments: 12,
  },
  {
    id: 4,
    author: "Diana Prince",
    content: "Coffee and code - the perfect combination for a productive morning. What's your favorite way to start the day?",
    timestamp: "8 hours ago",
    likes: 18,
    comments: 9,
    imageUrl: "https://images.pexels.com/photos/1181244/pexels-photo-1181244.jpeg?auto=compress&cs=tinysrgb&w=400"
  },
  {
    id: 5,
    author: "Edward Wilson",
    content: "Attended an incredible tech conference today. So many innovative ideas and inspiring people. The future of technology looks bright!",
    timestamp: "1 day ago",
    likes: 33,
    comments: 15,
  },
  {
    id: 6,
    author: "Fiona Davis",
    content: "Weekend hiking adventure complete! Nature has a way of clearing the mind and refreshing the soul. üèîÔ∏è",
    timestamp: "2 days ago",
    likes: 28,
    comments: 6,
    imageUrl: "https://images.pexels.com/photos/1366919/pexels-photo-1366919.jpeg?auto=compress&cs=tinysrgb&w=400"
  }
];

export default function MomentsPage() {
  const [posts, setPosts] = useState(dummyPosts);
  const [newPost, setNewPost] = useState("");

  const handleLike = (postId: number) => {
    setPosts(posts.map(post => 
      post.id === postId 
        ? { ...post, likes: post.likes + 1 }
        : post
    ));
  };

  const handleAddPost = () => {
    if (!newPost.trim()) return;
    
    const post: Post = {
      id: Date.now(),
      author: "You",
      content: newPost,
      timestamp: "Just now",
      likes: 0,
      comments: 0,
    };
    
    setPosts([post, ...posts]);
    setNewPost("");
  };

  return (
    <div className="max-w-2xl mx-auto">
      {/* Header */}
      <div className="mb-6">
        <h2 className="text-2xl font-bold mb-4">Moments</h2>
        
        {/* New Post */}
        <div className="bg-card p-4 rounded-lg border border-border mb-6">
          <textarea
            placeholder="What's on your mind?"
            value={newPost}
            onChange={(e) => setNewPost(e.target.value)}
            className="w-full p-3 border border-input rounded-lg bg-background text-foreground placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:border-transparent resize-none"
            rows={3}
            maxLength={500}
          />
          <div className="flex justify-between items-center mt-3">
            <span className="text-xs text-muted-foreground">
              {newPost.length}/500
            </span>
            <Button 
              onClick={handleAddPost} 
              disabled={!newPost.trim()}
              size="sm"
            >
              Post
            </Button>
          </div>
        </div>
      </div>

      {/* Posts Feed */}
      <div className="space-y-6">
        {posts.map((post) => (
          <article key={post.id} className="bg-card p-6 rounded-lg border border-border">
            {/* Post Header */}
            <div className="flex items-center mb-4">
              <div className="w-10 h-10 rounded-full bg-primary flex items-center justify-center text-primary-foreground font-bold mr-3">
                {post.author.charAt(0).toUpperCase()}
              </div>
              <div>
                <h3 className="font-semibold text-foreground">{post.author}</h3>
                <p className="text-sm text-muted-foreground">{post.timestamp}</p>
              </div>
            </div>

            {/* Post Content */}
            <div className="mb-4">
              <p className="text-foreground leading-relaxed">{post.content}</p>
              {post.imageUrl && (
                <div className="mt-4">
                  <img 
                    src={post.imageUrl} 
                    alt="Post image" 
                    className="w-full rounded-lg max-h-96 object-cover"
                    onError={(e) => {
                      e.currentTarget.style.display = 'none';
                    }}
                  />
                </div>
              )}
            </div>

            {/* Post Actions */}
            <div className="flex items-center space-x-6 pt-3 border-t border-border">
              <button
                onClick={() => handleLike(post.id)}
                className="flex items-center space-x-2 text-muted-foreground hover:text-primary transition-colors"
              >
                <span className="text-sm">üëç</span>
                <span className="text-sm">{post.likes} Like{post.likes !== 1 ? 's' : ''}</span>
              </button>
              
              <button className="flex items-center space-x-2 text-muted-foreground hover:text-primary transition-colors">
                <span className="text-sm">üí¨</span>
                <span className="text-sm">{post.comments} Comment{post.comments !== 1 ? 's' : ''}</span>
              </button>
              
              <button className="flex items-center space-x-2 text-muted-foreground hover:text-primary transition-colors">
                <span className="text-sm">üîó</span>
                <span className="text-sm">Share</span>
              </button>
            </div>
          </article>
        ))}
      </div>

      {/* Load More */}
      <div className="text-center mt-8">
        <Button variant="outline" size="lg">
          Load More Posts
        </Button>
      </div>
    </div>
  );
}
